.header {
  background-color: #000000;
  display: flex;
  align-items: center;
  position: sticky;
  padding: 5px 20px 5px 10px;
  top: 0;
  z-index: 100;
}
.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 10px;
}
.header__search {
  display: flex;
  flex: 1;
}
.header__nav {
  display: flex;
  justify-content: space-evenly;
}
.header__option {
  display: flex;
  flex-direction: column;
  margin: 0 10px;
}
.header__optionLineOne {
  font-size: 10px;
}
.header__optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}
.header_optionBasket {
  display: flex;
  align-items: center;
}
.header__link {
  color: white;
  text-decoration: none;
}
.header__searchInput {
  height: 12px;
  padding: 10px;
  border: none !important;
  flex: 1;
}
/* .header__searchInput:focus {
  appearance: none;
  border-color: teal;
} */
.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: orange;
}
